import { FunctionComponent } from 'react';
import { TextField, Typography } from '@mui/material';

interface TFWithLabelAndLinkProps {
    title?: string | '';
    label?: string | '';
    link?: string | '';
    labelPlaceholder?: string | '';
    linkPlaceholder?: string | '';
    onChange?: any;
    id?: number;
}

const TFWithLabelAndLink: FunctionComponent<TFWithLabelAndLinkProps> = ({ title, label, link, labelPlaceholder, linkPlaceholder, onChange, id }) => {
    return (
        <div className='inline-start'>
            <Typography variant='body1' style={{ marginBottom: 8 }}>{title}</Typography>
            <TextField id='outlined-basic' placeholder={labelPlaceholder} variant='standard' size='small' style={{
                marginBottom: 8,
                marginLeft: 8,
                display: 'flex',
                flex: 2,
            }}
                InputProps={{
                    disableUnderline: true
                }}
                value={label}
                onChange={(e) => onChange(id, 'value', e.target.value)}
            />
            <div>
                <TextField id='outlined-basic' placeholder={linkPlaceholder} variant='standard' size='small' style={{
                    marginBottom: 8,
                    marginLeft: 8,
                    display: 'flex',
                }}
                    InputProps={{
                        disableUnderline: true
                    }}
                    value={link}
                    onChange={(e) => onChange(id, 'link', e.target.value)}
                />
            </div>
        </div>
    );
}

export default TFWithLabelAndLink;